<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span.text-white 2
      h1 Programacin en Unity3D
    
    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema2/img-1.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-right")
        p.mb-0(data-aos="fade-left") El programa Unity es un motor de tipo grfico; esto quiere decir que contiene muchas funcionalidades que resuelven aspectos en la creacin de un videojuego sin que se necesite programar; por ejemplo poner color a un objeto seleccionado, iluminar un plano o cambiar el nivel de una imagen entre otros.
          br
          br
          |Para adicionar algn tipo de comportamiento, componente o atributo que no se pueda hacer desde el men del programa, lo que se debe hacer es escribir lneas de cdigo o <em>scripts</em> para que le proporcionen estas caractersticas. Esto es lo que se llama programacin orientada a objetos.

    p.mb-5(data-aos="fade-right") <b>La Programacin Orientada a Objetos -POO-</b> posee las siguientes caractersticas:

    TabsA.color-acento-contenido.mb-5(data-aos="fade-left")
      .tarjeta.p-4(titulo="Abstraccin" style="background-color: #DFF5FA")
        p Consiste en abstraer o aislar las caractersticas o atributos esenciales que definen un objeto y que lo distinguen de los dems con el propsito de que las pueda usar otro objeto. Por ejemplo si el objeto es una puerta las caractersticas esenciales es que tenga un marco, bisagras y cerradura sin importar el tamao, el color y el tipo de material entre otras.
      .tarjeta.p-4(titulo="Herencia" style="background-color: #DFF5FA")
        p Al encapsulamiento de las caractersticas de un objeto se le llama clase; que est conformada por un patrn de atributos que la hacen de orden superior o superclase porque tiene caractersticas generales, pero pueden surgir clases hijas o subclases que hereden estas caractersticas y se encuentren en orden inferior dentro de la jerarqua. 
          br
          br
          |En el ejemplo de la puerta, las clases hijas que heredan las caractersticas son los tipos de puertas que pueden elaborarse como las correderas, plegables, pivotantes, de seguridad, entre otras.
      .tarjeta.p-4(titulo="Polimorfismo" style="background-color: #DFF5FA")
        p Consiste en definir un mtodo con un conjunto de parmetros el cual puede ser aplicado en varios tipos de objetos. El mtodo puede aumentar o disminuir los parmetros dependiendo del objeto al que se aplique. Por ejemplo el mtodo se llama Open y es utilizado para abrir diferentes objetos como: puerta, cuenta bancaria, evento, historia clnica, entre otros. Dependiendo de lo que se abra, el proceso como tal, tiene ms o menos actividades por realizar.
      .tarjeta.p-4(titulo="Encapsulamiento" style="background-color: #DFF5FA")
        p Es el conjunto de caractersticas esenciales que han sido separadas de las no esenciales. En el ejemplo de la puerta en el encapsulamiento se encuentra el marco, las bisagras y la cerradura ya que son los atributos principales o generales que hacen que el objeto sea definido como puerta, los dems atributos como el color, el material y el tamao, hacen parte lo que se consideran caractersticas, pues estos son atributos que pueden poseer innumerables tipos de objetos.

    p.mb-5(data-aos="fade-right") El lenguaje de programacin que se utiliza en Unity para generar las lneas de cdigo o <em>Scripts</em> es C#; el cual se compone de variables, funciones y clases que se explicarn ms adelante. Para escribir el cdigo, Unity tiene un editor de texto asociado llamado Visual Studio, aunque el programador puede utilizar simplemente el bloc de notas. El editor (Visual Studio) le ayuda a determinar los errores de sintaxis que se puedan cometer en el desarrollo, por esa razn es recomendable hacer uso de este.   
      br
      br
      |Antes de comenzar a estructurar un programa en Unity, ver la explicacin de algunos conceptos bsicos en el siguiente enlace:
    
    .tarjeta.p-3.mb-5.col-lg-10.mx-auto(style="background-color: #C0EBF6" data-aos="fade-left")
      .row.justify-content-around.align-items-center
        img(src="@/assets/curso/temas/tema2/img-2.svg" style="max-width: 98px")
        .col
          .row.justify-content-between.align-items-center
            .col.mb-3.mb-sm-0
              h3.mb-3 Fundamentos de programacin: algoritmos, estructura de datos y objetos
              p.text-small Consultar en la base de datos SENA, ingresando su usuario y contrasea.
            .col-sm-auto
              a.boton.color-acento-botones(href="https://www-ebooks7-24-com.bdigital.sena.edu.co/?il=10409" target="_blank")
                span.pe-2 Enlace web
                img(src="@/assets/curso/temas/decorador-3.svg" style="max-width: 15px")

    Separador
    .titulo-segundo.color-acento-contenido
      h2#t_2_1 2.1 Conceptos bsicos de algoritmo y programa
    
    p.mb-5(data-aos="fade-right") Para introducirse en el mundo de la programacin, es necesario comprender los procedimientos y tcnicas para desarrollar programas, teniendo claro de antemano que estos (programas) se conciben como la solucin a un problema o situacin determinada. A continuacin se exponen los pasos fundamentales que conducen al desarrollo de un <em>software</em>:
    
    img.mb-5.mx-auto(src="@/assets/curso/temas/tema2/img-3.svg" style="max-width: 400px" data-aos="fade-left" alt="Imagen que muestra los pasos fundamentales en el desarrollo de software")
    .row.mb-5
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/n-1.svg' style="max-width: 138px")
          .tarjeta(style="background-color: #E1DDFC")
            .p-4
              h4.text-center Analizar el problema o situacin
              p Consiste en apoyar al desarrollador en la comprensin del contexto y las variables que determinan la situacin o problema presentado. Es la identificacin de los datos iniciales que se deben procesar para llegar a la solucin.

      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/n-2.svg' style="max-width: 138px")
          .tarjeta(style="background-color: #E1DDFC")
            .p-4
              h4.text-center Disear el algoritmo
              p Consiste en definir una serie de acciones a ejecutar en el proceso y que deben culminar con la solucin del problema.

      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/n-3.svg' style="max-width: 138px")
          .tarjeta(style="background-color: #E1DDFC")
            .p-4
              h4.text-center Realizar la programacin
              p Es la codificacin del algoritmo en un lenguaje de programacin que sea interpretado y ejecutado por el ordenador para que se lleve a cabo la solucin del problema.
      
    .cajon.color-primario.p-4.mb-5.col-lg-10.mx-auto(style="background-color: #C0EBF6")
      p(data-aos="fade-right") Para lograr el objetivo de solucionar un problema o tarea, se debe analizar la situacin para tener claridad de lo que se pretende, se disea un algoritmo con los pasos a seguir y se realiza la programacin con lneas de cdigo escritas en un lenguaje para ser procesadas y ejecutas por un ordenador del cual se obtienen los resultados esperados.

    .d-flex.flex-wrap.align-items-center.mb-5
      img(src="@/assets/curso/temas/decorador-titulo.svg" style="max-width: 40px; max-height: 40px;")
      h3.mb-0.ps-3 Algoritmo
    
    p.mb-5(data-aos="fade-right") Es el conjunto de pasos o instrucciones ordenadas lgicamente para resolver un problema o realizar una tarea especfica. Es uno de los pasos previos antes de entrar a la programacin, es decir, antes escribir las lneas de cdigo. Un algoritmo posee caractersticas muy definidas para que resulte efectivo:

    ol.lista-ol--cuadro.lista-ol--separador.col-xl-8.mx-auto.mb-5
      li 
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span a
        | <b>Preciso:</b> tener un objetivo claramente definido.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span b
        | <b>Secuencial:</b> llevar a cabo una serie de pasos en secuencia y siguiendo un orden lgico.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span c
        | <b>Finito:</b> poseer un nmero delimitado de pasos.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span d
        | <b>Determinado:</b> obtener un mismo resultado no importa cuntas veces se ejecute.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span e
        | <b>Llegar a un resultado:</b> solucionar el problema detectado o resolver la tarea que se ha planteado.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span f
        | <b>Corto:</b> analizar los tiempos para optimizar los recursos de programacin.
    
    p.mb-5 Los algoritmos pueden ser:

    .tarjeta--container.row.mb-5
      .col-md.tarjeta.color-acento-contenido.p-5.texto-blanco
        .row.justify-content-center.mb-5
          .col-6
            figure
              img.mx-auto(src='@/assets/curso/temas/tema2/img-4.svg' style="max-width: 213px" data-aos="fade-right" alt="Imagen que muestra los pasos lgicos en el desarrollo de software sin operaciones matemticas")
          
        h2.text-center Cualitativos
        p.mx-auto(style="max-width: 400px" data-aos="fade-left").text-center Cuando dentro de los pasos lgicos que lo conforman no se involucran operaciones matemticas para obtener el resultado deseado.

      .col-md.tarjeta.color-primario.p-5
        .row.justify-content-center.mb-5
          .col-6
            figure
              img.mx-auto(src='@/assets/curso/temas/tema2/img-5.svg' style="max-width: 213px" data-aos="fade-right" alt="Imagen que muestra los pasos lgicos en el desarrollo de software con operaciones y clculos matemticos")
          
        h2.text-center(style="color: black") Cuantitativos
        p.mx-auto(style="max-width: 400px; color: black" data-aos="fade-left").text-center Cuando dentro de los pasos lgicos se involucra operaciones y clculos matemticos que son necesarios para obtener el resultado esperado.
    p.mb-5(data-aos="fade-right") Y un algoritmo se puede disear utilizando la tcnica:

    .tarjeta--container.row.mb-5
      .col-md.tarjeta.color-primario.p-5
        .row.justify-content-center.mb-5
          .col-6
            figure
              img.mx-auto(src='@/assets/curso/temas/tema2/img-6.svg' style="max-width: 213px" data-aos="fade-right" alt="Imagen que ilustra instrucciones paso a paso representadas en forma descriptiva a travs de expresiones proposicionales")
          
        h2.text-center(style="color: black") Pseudocdigo
        p.mx-auto(style="max-width: 400px; color: black" data-aos="fade-left").text-center Las instrucciones que se deben seguir paso a paso de manera lgica y ordenada se representan en forma descriptiva elaborando una serie de expresiones a manera de proposicin o afirmacin sobre un evento.

      .col-md.tarjeta.color-acento-contenido.p-5.texto-blanco
        .row.justify-content-center.mb-5
          .col-6
            figure
              img.mx-auto(src='@/assets/curso/temas/tema2/img-7.svg' style="max-width: 213px" data-aos="fade-right" alt="Imagen que muestra la representacin de instrucciones mediante smbolos con formas especficas segn la accin deseada")
          
        h2.text-center Diagrama de flujo
        p.mx-auto(style="max-width: 400px" data-aos="fade-left").text-center Las instrucciones son representadas por medio de smbolos con formas especficas de acuerdo con la accin que se desea realizar. Este concepto se ampliar en el siguiente apartado.

    p.mb-5(data-aos="fade-right") <b>Ejemplo</b>
      br
      br
      |En la historia <b>La isla de los tesoros escondidos</b>, se debe disear un algoritmo de bsqueda para encontrar el primer tesoro de cinco (5), el jugador pasar al siguiente nivel solo si lo ha encontrado y podr terminar la primera fase del juego:
    h4.col-xl-10.mx-auto.mb-4 Pseudocdigo
    ol.lista-ol--cuadro.lista-ol--separador.col-xl-10.mx-auto.mb-5
      li 
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 1
        | <b>Preciso:</b> inicio.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 2
        | <b>Paso 2:</b> mirar en el mapa los datos de la ubicacin actual y las rutas que puede seguir.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 3
        | <b>Paso 3:</b> seleccionar una ruta.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 4
        | <b>Paso 4:</b> emprender la bsqueda.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 5
        | <b>Paso 5:</b> encontr un tesoro?.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 6
        | <b>Paso 6:</b> Si es verdad pasar al siguiente nivel y terminar fase, de lo contrario volver al paso 2 y repetir la secuencia hasta encontrar el tesoro.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span 7
        | <b>Paso 7:</b> fin.
    p.mb-5(data-aos="fade-right") Ms adelante se ver este mismo algoritmo expresado en forma de diagrama de flujo para comprender la diferencia.

    .d-flex.flex-wrap.align-items-center.mb-5
      img(src="@/assets/curso/temas/decorador-titulo.svg" style="max-width: 40px; max-height: 40px;")
      h3.mb-0.ps-3 Programa
    
    p.mb-5(data-aos="fade-right") Debido a que el diseo del algoritmo representado en forma de pseudocdigo o diagrama de flujo, no es comprensible por ningn ordenador que vaya a procesar las instrucciones, estas se deben traducir a un lenguaje entendible. Entonces:
    
    .cajon.color-primario.p-4.mb-5.col-lg-10.mx-auto(style="background-color: #C0EBF6")
      p(data-aos="fade-right") Un <b>programa</b> es la codificacin de las instrucciones de un algoritmo en un lenguaje de programacin, con una sintaxis correcta para que pueda ser interpretado por el ordenador.
    
    p.mb-5(data-aos="fade-right") La sintaxis correcta se refiere a que cada lenguaje de programacin tiene ciertas reglas semnticas que se deben respetarse para que en el momento de ser procesadas por la computadora no se generen errores que impidan lograr los resultados esperados. Al proceso de escribir las lneas de cdigo del programa se le denomina <b>programacin</b> y las personas que se dedican a esta labor se les denomina <b>programadores</b>.

    TabsA.color-acento-contenido.mb-5(data-aos="fade-left")
      .tarjeta.p-4(titulo="Caractersticas de un <br> lenguaje de programacin" style="background-color: #DFF5FA")
        p  Poseen reglas de sintaxis y semntica que se debe seguir al pie de la letra para que pueda ser interpretado por la computadora.
          br
          br
          | Son de alto nivel porque son comprendidos por cualquier persona cuando los escribe al seguir las reglas por ejemplo Contador = 10; asigna el valor de 10 a la variable Contador.
          br
          br
          | Deben ser traducidos a un lenguaje que entienda el ordenador para que se conviertan en lenguajes ejecutables. 
      .tarjeta.p-4(titulo="Tipos de programas" style="background-color: #DFF5FA")
        p  Pueden ser de dos tipos:
          br
          br
          | Sistema: son un conjunto de instrucciones que sirven para gestionar el funcionamiento del ordenador como la Unidad Central de Procesos - CPU-, los recursos de memoria como el disco duro y la memoria RAM, perifricos como impresoras y dispositivos de comunicacin entre otros. 
          br
          br
          | Aplicacin: conjunto de instrucciones escritas por programadores para que se ejecuten tareas especficas, por ejemplo procesar textos (Word), realizar clculos (Excel), hacer presentaciones (PowerPoint), desarrollar videojuegos (Unity); entre muchos otros.
    p.mb-5(data-aos="fade-right") El proceso que se lleva a cabo para que un programa sea ejecutado por el ordenador y realice la funcin deseada se muestra a continuacin:

    ImagenInfografica.col-xl-8.mx-auto.color-acento-botones.mb-5(data-aos="fade-left")
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/temas/tema2/img-8.svg' alt="secuencia de 7 pasos para la ejecucin del programa en un ordenador")

      .tarjeta.p-3(x="11.5%" y="20%" numero="+" style="background-color: #DFF5FA")
        p.mb-0 Es la escritura ordenada y lgica de las instrucciones que se deben llevar a cabo para resolver un problema o situacin determinada, representado en forma de pseudocdigo o de diagrama de flujo.
      .tarjeta.p-3(x="11.5%" y="57%" numero="+" style="background-color: #DFF5FA")
        p Codificacin del algoritmo a travs de un editor de cdigo que sirve para escribir las instrucciones del algoritmo en un lenguaje de programacin de alto nivel, llamado programa fuente.
      .tarjeta.p-3(x="11.5%" y="95%" numero="+" style="background-color: #DFF5FA")
        p Al programa fuente se aplica la opcin de compilar del editor de cdigo utilizado, para que el ordenador a travs de un lenguaje intrprete (interno en la mquina) lo convierta en un programa objeto que puede entender la mquina.
      .tarjeta.p-3(x="50%" y="20%" numero="+" style="background-color: #DFF5FA")
        p Es el programa que resulta de enlazar todas las funciones para convertir el programa objeto en un programa que la computadora pueda ejecutar.
      .tarjeta.p-3(x="50%" y="95%" numero="+" style="background-color: #DFF5FA")
        p Es el programa que se obtiene producto del proceso de compilacin que ha realizado la mquina. Si el programa fuente llega a tener algn error de sintaxis cometido por el programador, el compilador entrega los errores generados para que sean corregidos y se vuelva a realizar la compilacin.
      .tarjeta.p-3(x="89%" y="20%" numero="+" style="background-color: #DFF5FA")
        p El programa est listo para ser ejecutado por el ordenador y producir  los resultados que se esperan de este.
    Separador

    .titulo-segundo.color-acento-contenido
      h2#t_2_2 2.2 Diagramas de flujo
    
    p.mb-5(data-aos="fade-right") Es una herramienta de tipo grfica que representa el diseo de un algoritmo y est compuesta por smbolos que indican las acciones a realizar para obtener un resultado que resuelve un problema, situacin o tarea. Los smbolos estn unidos por flechas las cuales sealan la secuencia lgica de ejecucin de dichas acciones. Todo algoritmo tiene un comienzo y un final representados por sus smbolos respectivos. A continuacin se explica cada uno de ellos, ver tabla 2:

    .titulo-sexto.color-acento-contenido
      h5 Tabla 2
      span.d-block Smbolos utilizados en los diagramas de flujo
    
    .tabla-b.color-acento-contenido.mb-5.col-xl-10.mx-auto(data-aos="fade-right")
      .tabla-b__header.d-flex
        h4.mb-0.text-white.text-center(style="width: 28%") Smbolo
        h4.mb-0.text-white.col.text-center Descripcin
      table
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-1.svg' style="max-width: 97px")
          td.text-start Indica el inicio y la terminacin del algoritmo.
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-2.svg' style="max-width: 150px")
          td.text-start Smbolo de entrada de datos, indica los valores iniciales que debern ingresar para ser procesados.
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-3.svg' style="max-width: 150px")
          td.text-start Realizacin de un proceso llevado a cabo con los datos introducidos, indica operaciones matemticas de las cuales se genera un resultado; por ejemplo: A  B + C el resultado de sumarle B a C se almacena en la variable A. 
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-4.svg' style="max-width: 97px")
          td.text-start Smbolo que indica la toma de una decisin. Al interior de l se coloca una pregunta que puede ser cierta/falsa y de la cual salen dos flechas que indica la ruta a seguir de acuerdo con la respuesta.
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-5.svg' style="max-width: 77px")
          td.text-start Smbolos que indican haber alcanzado la solucin de la tarea, evento o problema. Al interior se coloca la respuesta obtenida.
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-6.svg' style="max-width: 97px")
          td.text-start Conector que se utiliza para enlazar dos partes del diagrama de flujo que se encuentran en una misma pgina o interfaz (pantalla) de usuario.
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-7.svg' style="max-width: 97px")
          td.text-start Conector que enlaza dos partes de un diagrama de flujo donde sus partes se encuentra en pginas diferentes.
        tr
          td(style="width: 20%")
            img.mx-auto(src='@/assets/curso/temas/tema2/t-8.svg' style="max-width: 138px")
          td.text-start Flechas que conectan los procesos e indican el flujo del diagrama de flujo.
    p.mb-5(data-aos="fade-right") Los diagramas de flujo tienen unas reglas de elaboracin, las cuales son:

    ol.lista-ol--cuadro.lista-ol--separador.col-xl-10.mx-auto.mb-5
      li 
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span a
        | Se deben elaborar en orden lgico que empieza arriba y termina abajo.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span b
        | Inician y finalizan con el mismo smbolo.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span c
        | Las flechas que se coloquen deben indicar el flujo o secuencia de las acciones a realizar.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span d
        | Los smbolos que indican decisin deben tener dos lneas de salida; una cuando la pregunta o afirmacin que se coloca al interior es verdadera la cual debe tener encima la palabra SI y la otra en el caso contrario y a la cual se le debe colocar encima la palabra NO.
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span e
        | Lo que se escriba al interior de los smbolos debe tener el menor nmero de palabras posibles o contener operaciones claras:  Z  A  B
      li
        .lista-ol--cuadro__vineta(data-aos="fade-right")
          span f
        | El diagrama debe probarse con datos reales para verificar su funcionamiento. A esta verificacin se le llama prueba de escritorio.
    p.mb-5(data-aos="fade-right") Las ventajas que tiene elaborar un algoritmo utilizando esta tcnica, son las siguientes:

