    p.mb-5(data-aos="fade-right") En los lenguajes de programacin dentro de la sintaxis de las lneas de cdigo; se utilizan elementos o identificadores los cuales son utilizados dentro de la estructura del programa para lograr la solucin del problema, situacin o tarea determinada. A continuacin, se estudiar cada uno de ellos.

    .d-flex.flex-wrap.align-items-center.mb-5
      img(src="@/assets/curso/temas/decorador-titulo.svg" style="max-width: 40px; max-height: 40px;")
      h3.mb-0.ps-3 Variables
    
    p.mb-5(data-aos="fade-right") Son espacios en la memoria del ordenador con un nombre que las identifica (identificador); los cuales son destinados para el almacenamiento de datos que pueden tomar diferentes tipos de valores durante la ejecucin del programa. 
      br
      br
      |Los <b>tipos de datos</b> que pueden almacenar las variables son:
    
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta-edit" data-aos="fade-left")
      .row(titulo="Numricos")
        .mb-4.mb-md-0
          p.col-lg-10.mx-auto Representados por valores enteros o valores reales que incorporan el signo, por ejemplo el valor -5 corresponde a un nmero entero con signo negativo y 4,5 corresponde a un valor real con un solo decimal y sin signo.
      .row(titulo="Lgicos")
        .mb-4.mb-md-0
          p.col-lg-10.mx-auto Son tipos de datos que representa uno de dos valores falso o verdadero, utilizados en los algoritmos cuando se disean instrucciones en las cuales se debe cumplir una condicin para la toma de una decisin; por ejemplo la condicin es encontrar el tesoro; si la respuesta es verdadera puede continuar al siguiente nivel y si es falsa debe seguir buscando.
      .row(titulo="Alfanumrico")
        .mb-4.mb-md-0
          p.col-lg-10.mx-auto Son datos representados por caracteres diferentes a los nmeros puros o a los lgicos; pues estos pueden contener letras con nmeros y smbolos especiales como guion (-), asterisco (*), smbolo de nmero (#), entre otros.
    
    .cajon.color-primario.p-4.mb-5.col-lg-10.mx-auto(style="background-color: #C0EBF6")
      p(data-aos="fade-left") Dentro de las lneas de cdigo del programa se debe indicar el tipo de variable que se est creando; es decir qu tipo de valor almacenar;  a esto se le llama declarar una variable. Por ejemplo si la variable se declara tipo numrica, entonces no podr contener caracteres.
    
    p.mb-5(data-aos="fade-right") En el lenguaje de programacin de C# utilizado en el programa de Unity para el desarrollo de videojuegos, se deben tener en cuenta una serie de <b>reglas para definir variables:</b>

    ul.mb-5
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 El nombre de la variable no puede comenzar con un nmero pero si puede hacer parte del nombre.
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 No se pueden colocar espacios dentro del nombre.
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 No puede contener caracteres como +, - que los cuales son utilizados en operaciones matemticas.
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 No pueden existir dos variables que se llamen igual  dentro de un mismo bloque de instrucciones.
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 No se pueden utilizar nombres con palabras propias del lenguaje de programacin; por ejemplo la no se puede llamar a una variable int  pues esta es una palabra que reserva el programa para definir que el tipo de datos es entero. 
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 El lenguaje hace diferencia entre letras maysculas y minsculas; por ejemplo las variables X y x son diferentes.
      li.d-flex.slign-items-center
        img(src='@/assets/curso/temas/decorador-2.svg' style="max-width: 25px" data-aos="fade-right")
        span.ps-3 Se recomienda darle un valor inicial a la variable.
    
    .conatiner-left-right.mb-5(style="background-color: #F7F6FE")
