      h3.mb-0.ps-3 Operadores
    p.mb-5(data-aos="fade-right") Son elementos dentro de la programacin, con los cuales se realizan operaciones bsicas sobre los datos. Cuando el programa fuente es compilado el ordenador realiza internamente los procedimientos matemticos o lgicos para entregar el resultado.
  
    .tabla-b.color-acento-contenido.mb-5(data-aos="fade-right")
      .tabla-b__header.d-flex
        h4.mb-0.text-white.text-center(style="width: 28%") Operador
        h4.mb-0.text-white.col.text-center Operandos
      table
        tr
          td Es el elemento como tal, con el que se realiza la operacin de adicin, substraccin, comparacin:
            br
            |+,-,<,>
          td Son los elementos sobre los cuales se aplican las operaciones:
            br
            |<b>variables, nmeros</b>

    .p-4.mx-auto.mb-5(style="background-color: #E1DDFC; max-width: 400px" data-aos="fade-left")
      p   Lo que se quiere es sumar las variables A,B y colocar el resultado en la variable Total
        br
        |  Limpiar los valores  
        br
        br
        |<b>A = ____</b>
        |<b>B = ____</b>
        br
        br
        |<b>Total = _____________</b>
        br
        br
        |<b>Limpiar (A y B): _________</b>
    
    p.mb-5(data-aos="fade-right") En el lenguaje de programacin C# el cual se utiliza en Unity para el desarrollo de los videojuegos, se distinguen los siguientes <b>tipos de operadores</b>:

    TabsA.color-acento-contenido.mb-5(data-aos="fade-left")
      .tarjeta.p-4(titulo="Operadores de Asignacin" style="background-color: #DFF5FA")
        p.mb-0 Se utilizan para asignar un nuevo valor a una variable que ha sido declarada dentro del programa. En la izquierda de la asignacin se coloca el nombre de la variable y en la derecha el valor que se asigna. Ejemplo:
          br
          br
          |<span style="color: red">string</span> logo;   se ha declarado la variable logo de tipo texto.
          br
          |<span style="color: red">int</span>      total;   se ha declarado la variable total de tipo numrico.
          br
          br
          |logo = ETR;  se ha asignado a la variable logo (izquierda)  el valor ETR (derecha).
          br
          |total = 28;     se  ha asignado a la variable total (izquierda)  el valor 28 (derecha).
      .tarjeta.p-4(titulo="Operadores Aritmticos" style="background-color: #DFF5FA")
        p.mb-0 Estn conformados por los smbolos que permiten hacer operaciones matemticas con los nmeros como suma (+), resta (-), multiplicacin (*), divisin (/); modular (%); este ltimo devuelve como resultado el residuo de una divisin. Ejemplo: 
          br
          br
          |<span style="color: red">Long</span> 	A;  se ha declarado la variable A de tipo entero almacenando nmeros muy grandes, diferente a la tipo int que almacena nmeros enteros muy pequeos.
          br
          |<span style="color: red">Int</span> 	B;  se ha declarado la variable B de tipo entero.
          br
          |<span style="color: red">Double</span> C;  se ha declarado la variable C de tipo decimal albergando hasta quince nmeros decimales despus del punto. 
          br
          br
          |A = 25 * 30;
          br
          |B = 5 * 3;
          br
          |C = 3 / 2;
      .tarjeta.p-4.d-flex.flex-wrap(titulo="Operadores Relacionales" style="background-color: #DFF5FA")
        .col-lg-7.order-2.order-lg-1
          p Estos operadores se utilizan para realizar la comparacin entre dos valores donde el resultado es verdadero si la afirmacin de la expresin comparada es correcta, de lo contrario el resultado es falso. Los smbolos para comparar las variables son:
          p Ejemplo:
            br
            |<span style="color: red">bool</span> total;   se ha declarado la variable total de tipo booleano es decir que solo puede tomar uno de dos valores; verdadero o falso.
            br
            br
            |total = 8 > 9 ;  esto da como resultado falso (<em>false</em>).
        .col-lg-5.order-1.order-lg-2.mb-4.mb-lg-0.d-flex.ps-lg-3
          tabla.tabla-edit-2
            thead(style="border: 1px solid black")
              tr
                th Operador
                th Ejemplo
                th Descripcin
            tbody
              tr
                td <
                td a < b
                td a menor que b
              tr
                td >
                td a > b
                td a mayor que b
              tr
                td 
                td a  b
                td a menor o igual que b
              tr
                td 
                td a  b
                td a mayor o igual que b
              tr
                td ==
                td a == b
                td a igual que b
              tr
                td !=
                td a != b
                td a distinto que b
      .tarjeta.p-4(titulo="Operadores Lgicos" style="background-color: #DFF5FA")
        p Estos operadores se utilizan con las tablas de verdad;
          ul
            li  El operador && (AND) es decir y ; solo devuelve un valor verdadero si las dos expresiones son verdad. 
            li  El operador || (OR) es decir o; solo devuelve un valor verdadero si una de las dos expresiones es verdadera.
            li  El operador ! (NOT) es decir no; devuelve un valor verdadero si la expresin es falsa y devuelve un valor falso si la expresin es verdadera.
          br
          br
          |Ejemplo: 
          br
          |<span style="color: red">Int</span> a = 10;  se ha declarado la variable a de tipo entero.
          br
          |<span style="color: red">Int</span> b = 20;  se ha declarado la variable b de tipo entero.
          br
          |<span style="color: red">Int</span> c = 8;  se ha declarado la variable c de tipo entero.
          br
          |<span style="color: red">bool</span> total;   se ha declarado la variable total de tipo booleana.
          br
          br
          |total = a < b && b > c;   el resultado es verdadero ya que a < b es verdad y b > c tambin es verdadero.
      .tarjeta.p-4(titulo="Operadores de Incremento" style="background-color: #DFF5FA")
        p Estos operadores se llevan a cabo incrementando o disminuyendo el valor de la variable en una unidad. El operador que indica incremento es ++ y el que indica disminucin es . Ejemplo:
          br
          br
          |<span style="color: red">Int</span> a = 5;  se ha declarado la variable a de tipo entero.
          br
          br
          |a ++;  se ha incrementado la variable a en 1 y ahora su valor es 6.
          br
          |a ++;  se ha incrementado la variable a en 1 y ahora su valor es 7.
          br
          |a ++;  se ha incrementado la variable a en 1 y ahora su valor es 8.
          br
          |a  ;  se ha disminuido la variable a en 1 y ahora su valor es 7.
          br
    
