      img(src="@/assets/curso/temas/decorador-titulo.svg" style="max-width: 40px; max-height: 40px;")
      h3.mb-0.ps-3 Funciones y mtodos
    
    p.mb-5(data-aos="fade-right") Son procedimientos dentro de la programacin llamados bloques y en los cuales se escriben lneas de cdigo o instrucciones para llevar a cabo una determinada accin dentro del programa. Estos elementos le proporcionan una estructura modular al programa ya que lneas de cdigo pueden ser invocados en otra parte del programa, sin necesidad de repetirlas o tambin se pueden invocar en otros programas para no escribirlas de nuevo. La diferencia entre estos dos procedimientos radica en siguiente caracterstica:

    .col-lg-8.col-xl-6.row.mb-5.mx-auto
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/img-12.svg' style="max-width: 138px")
          .tarjeta.w-100(style="background-color: #E1DDFC")
            .p-3.text-center
              p.fw-bold Funcin
              p.text-center Es un bloque de instrucciones que cuando se ejecutan retornan siempre un valor de salida.
      .col-md-6.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema2/img-13.svg' style="max-width: 138px")
          .tarjeta.w-100(style="background-color: #E1DDFC")
            .p-3.text-center
              p.fw-bold Mtodo
              p.text-center Es un bloque de instrucciones que cuando se ejecutan no necesariamente retornan un valor.

    p.mb-5(data-aos="fade-right") De acuerdo con lo anterior, todos los bloques de instrucciones que lleven a cabo alguna accin dentro del programa se llaman mtodos.

    .conatiner-left-right.mb-5(style="background-color: #F7F6FE")
      .d-flex.flex-wrap.col-xl-10.mx-auto.py-4.mb-4
        p.col-lg-6.col-xl-8.pe-lg-3(data-aos="fade-right") <b>Ejemplo</b>
          br
          br
          |En el lenguaje de programacin C# (que se utiliza en Unity):
          br
          br
          |La instruccin para realizar la primera parte corresponde a una funcin dado que devuelve un valor, entonces:
          br
          br
          |La primera instruccin indica que el procedimiento que se va a realizar se llama local el cual es de tipo privado y devuelve un valor entero, contiene dos para parmetros: una variable de tipo entera llamada A y otra del mismo tipo llamada B.
        img.col-lg-6.col-xl-4(src="@/assets/curso/temas/tema2/img-14.svg" data-aos="fade-left")
      .d-flex.flex-wrap.col-xl-10.mx-auto.py-4
        img.col-lg-6.col-xl-4(src="@/assets/curso/temas/tema2/img-15.svg" data-aos="fade-right")
        p.col-lg-6.col-xl-8.ps-lg-3(data-aos="fade-left") Los procedimientos pueden ser de tipo private o public, es decir, si el bloque de instrucciones solo se puede utilizar en el programa actual es private, pero si se puede utilizar en otros es public. El procedimiento suma las variables A y B retornando el resultado en la variable total. Como hay un resultado que se genera el procedimiento es una <b>Funcin.</b>
          br
          br
          |La primera instruccin indica que el procedimiento que se va a realizar se llama limpiar el cual es de tipo privado (private) y retorna un valor nulo (void),  en las cajas de texto (txt) de las variables A,B y total. Obsrvese que como el procedimiento no devuelve un valor, entonces es un mtodo.

    .d-flex.flex-wrap.align-items-center.mb-5
      img(src="@/assets/curso/temas/decorador-titulo.svg" style="max-width: 40px; max-height: 40px;")
      h3.mb-0.ps-3 Estructuras de control
    
    p.mb-5(data-aos="fade-right") Las estructuras de control permiten que se ejecuten los procedimientos o bloques de instrucciones de una manera secuencial, condicional o repetitiva.

    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta-edit" data-aos="fade-left")
      .row(titulo="Estructura de control secuencial")
        .w-100
          p.col-lg-11.mx-auto.mb-4 Permite que las instrucciones de un procedimiento se ejecuten una tras otra, es decir en secuencia. 
            |Ejemplo: en lenguaje C#; las siguientes instrucciones se deben llevar a cabo en secuencia:
            br
            br
            | Ingresar el primer valor y en pantalla debe aparecer Ingresar el primer nmero:
            br
            | Ingresar el segundo valor y en pantalla debe aparecer Ingresar el segundo nmero:
            br
            | Sumar los dos valores ingresados y en pantalla debe aparecer  la suma es:
          img.mx-auto(src="@/assets/curso/temas/tema2/img-16.png" style="max-width: 892px" alt="cdigo en lenguaje C# para la representacin de una secuencia")
      .row(titulo="Estructura de control condicional")
        .w-100
          p.col-lg-11.mx-auto.mb-4 Permite que se ejecuten instrucciones siempre y cuando se cumpla una condicin y el formato de la sintaxis se escribe con los cdigos if y else.
            br
            |Ejemplo: en lenguaje C#: 
            br
            br
            |   La condicin es que la venta sea mayor a 50 dlares.
            br
            |   Si es verdadero se aplica descuento.
            br
            |   Si es falso no se aplica el descuento.
          img.mx-auto(src="@/assets/curso/temas/tema2/img-17.png" style="max-width: 892px" alt="cdigo en lenguaje C# para la representacin de condiciones")
      .row(titulo="Estructura repetitiva")
        .w-100
          p.col-lg-11.mx-auto.mb-4 Permite que se ejecuten un bloque de instrucciones varias veces hasta que se cumpla una condicin. La sintaxis se escribe con el cdigo while (mientras). 
            |Ejemplo: en lenguaje C#:
            br
            br
            |   Imprimir los mltiplos de 2.
            br
            |   Hasta que llegue a 20.
