    .titulo-segundo.color-acento-contenido
      h2#t_2_5 2.5 Estructura bsica de un <em>script</em> de Unity3D
    
    p.mb-5(data-aos="fade-right") El lenguaje de programacin que se utiliza en Unity es el lenguaje C# y a las lneas de cdigo que lo configuran se les llama <em>Scripts</em>, las cuales consisten en una secuencia de instrucciones que se escriben en un editor de texto que es adjuntado en Unity al hacer su instalacin en el PC; este editor de escritura es el Visual Studio. 
      br
      br
      |El objeto de escribir lneas de cdigo en Unity es para asignarle comportamientos especficos a los componentes del juego (<em>GameObjects</em>), crear eventos y desarrollar funciones que controlen acciones determinadas.
      br
      br
      |El lenguaje C# se compone de:

    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta-edit" data-aos="fade-left")
      .row.col-xl-11.mx-auto(titulo="Variables")
        .col-lg.order-2.order-lg-1
          p Como se mencion antes son espacios reservados en la memoria para almacenar valores de diferentes tipos, ya se vio en los anteriores apartados su tipologa (numricos, lgicos, alfanumricos), pero en Unity tambin hay tipos de datos de referencia que se aplican a los objetos como Transform y Light que se visualizan en la ventana del Inspector; donde la primera trata con aspectos de posicin, rotacin y tamao del objeto y la segunda con efectos de iluminacin.
          p Las variables deben ser declaradas al comienzo del bloque de las lneas de cdigo definiendo si van a ser pblicas o privadas. Una <b>variable pblica</b> es aquella que puede ser utilizada por otros <em>scripts</em> y cambiar su valor porque es accesible para ellos ya que aparece en la ventana del Inspector y una <b>variable privada</b> es aquella que solo puede ser utilizada dentro de una misma clase (ya se ver este trmino a continuacin) y dentro de un mismo mtodo o funcin y no puede ser visualizada en el Inspector porque sus valores no son manipulables por otros <em>scripts</em>.
          p Cuando se codifica una variable se debe tener en cuenta aspectos de nomenclatura como que el nombre no debe empezar por un nmero y  no debe tener espacios.
          a.col.boton.color-acento-botones(@click="imgSelect = 8; modal2 = true;")
            span Ampliar imagen
            img.ms-2(src="@/assets/curso/temas/decorador-4.svg" style="max-width: 15px")
        .col-lg-3.order-1.order-lg-2.mb-4.mb-lg-0
          img.mx-auto(src="@/assets/curso/temas/tema2/img-26.jpg" style="max-width: 261px" alt="Interfaz de Variables")
          em Referencias

      .row.col-xl-11.mx-auto(titulo="Funciones")
        p.mb-5 Una funcin en Unity es un procedimiento que realiza acciones sobre un objeto, a la funcin tambin se le llama mtodo y consiste en un bloque de lneas de cdigo que hacen un llamado a la accin sobre los componentes en los que se aplica. Cuando se va a construir un <em>script</em> en Unity se activa la clase <em>Mono Behaviour</em> que es la clase base la cual llama funciones automticas que se deben activar para iniciar el <em>script</em> como son:
          br
          br
          |<b>Awake:</b> funcin que se encarga de llamar a todas las variables declaradas en el <em>script</em> y que necesitan ser inicializadas con un valor. 
          br
          |<b>Start:</b>  funcin que es llamada para inicializar el procedimiento.
          br
          |<b>Update:</b> funcin que se utiliza para el movimiento de los objetos sin utilizar el mtodo fsica de Unity y se repite cada vez que se necesite para actualizar el brestado de este.
          br
          |<b>FixedUpdate:</b> funcin que se utiliza para controlar objetos cuando dependen de la fsica.
        .d-flex.flex-wrap.mb-5
          .col-lg.order-2.order-lg-1
            p <b>Ejemplo:</b> en la siguiente escena en Unity se van a adicionar unas lneas de cdigo para crear un evento sobre el plano y el cubo para lo cual se cre un archivo <em>script TestClick</em>  que al ser llamado despleg en la ventana del Inspector el llamado a la clase base <em>Mono Behaviour</em> que a su vez activo las funciones <em>Start y Update</em>.
            a.col.boton.color-acento-botones(@click="imgSelect = 9; modal2 = true;")
              span Ampliar imagen
              img.ms-2(src="@/assets/curso/temas/decorador-4.svg" style="max-width: 15px")
          .col-lg-5.order-1.order-lg-2.mb-4.mb-lg-0.ps-lg-4
            img.w-100(src="@/assets/curso/temas/tema2/img-27.jpg" alt="Interfaz Funciones")
            em Funciones
        p.w-100.mb-5 Para escribir lneas de cdigo que se refieran a funciones (mtodos) se deben tener en cuenta los siguientes aspectos:
          br
          |   Puede ser pblica o privada
          br
          |   Debe tener un identificador (nombre) 
          br
          |   Debe Incluir parmetros 
          br
          |   Debe retornar un tipo de dato as sea nulo
          br
          br
          |<b>Ejemplo:</b> En el editor de texto Visual Studio se ha creado un mtodo con identificador Multiplica, el cual contiene dos parmetros de tipo entero (a y b) cuya funcionalidad es multiplicar dos nmeros. El resultado que debe retornar el mtodo ( c ) es de tipo entero.
        .d-flex.flex-wrap.mb-5
          .col-lg.order-2.order-lg-1
            p El mtodo Multiplica es de tipo pblico es decir puede ser utilizado en otros <em>scripts</em> y el nombre del archivo es TestClick. cs y el programa ha llamado la clase MonoBehaviour que contiene las funciones de inicializacin por defecto:
              br
              br
              |public class TestClick.cs : MonoBehaviour {
            a.col.boton.color-acento-botones(@click="imgSelect = 10; modal2 = true;")
              span Ampliar imagen
