      .row.col-xl-11.mx-auto(titulo="Clases")
        p.mb-5 Son las que agrupan los atributos y caractersticas que van a contener los objetos que se crean en Unity; dentro de ellas se encuentran las variables y los mtodos (funciones) que se aplicarn para que se comporte como se ha planeado. 
          br
          br
          |El objeto se comporta de acuerdo con los atributos contenidos en la clase, lo que significa que esta (clase) acta como un molde para los objetos que se construyan dentro de ella.
          br
          br
          |Las clases cumplen con las caractersticas de abstraccionismo, encapsulamiento, heredad y polimorfismo propias de la Programacin Orientada a Objetos  POO-. Algunas de las clases ms importantes que se encuentran en el motor de Unity son:
        .d-flex.flex-wrap.mb-5
          .col-lg.order-2.order-lg-1
            p Cuando se estn escribiendo lneas de cdigo y se crean  varias clases para los objetos las cuales van agrupar diferentes atributos, entonces se utiliza lo que se llama Espacios de nombres; para organizar clases dentro de un mismo entorno, agrupndolas para darle una mejor estructura al <em>scripts</em> y evitar confusiones cuando el motor de Unity haga la compilacin de las instrucciones para ser ejecutadas.
          .col-lg-5.order-1.order-lg-2.mb-4.mb-lg-0.ps-lg-4
            tabla.tabla-edit-2
              thead(style="border: 1px solid black")
                tr
