    .titulo-segundo.color-acento-contenido
      h2#t_2_6 2.6 Control mediante interfaz de usuario (Inspector)
    
    p.mb-5(data-aos="fade-right") La interfaz de usuario es (UI) es la manera en que este interacciona con el programa; en este caso la forma en que un jugador se comunica con el videojuego interactuando con l. El motor de Unity posee un componente llamado Canvas que acta como un contenedor o plantilla donde se pueden colocar todos los elementos u objetos  que van a hacer parte de la interfaz del usuario. Canvas quieres decir lienzo y es el espacio donde se van a colocar imgenes, botones, textos, personajes, formas, entre otros que pueden ser controlados a travs de la ventana del inspector.
      br
      br
      |Para utilizar Canvas entonces en la interfaz inicial de Unity se realizan las siguientes acciones:
    
    .tarjeta.mb-5.p-4.px-md-5(style="background-color: #D8F3F9" data-aos="fade-left")
      SlyderA(tipo="b")
        .row
          .col-lg-4.mb-4.mb-lg-0
            p.mb-4  Clic en la opcin de men GameObject
              br
              | Clic en la opcin UI
              br
