            p.mb-4 Cuando se ha aadido Canvas a la escena en la ventana del Inspector se observa cada uno de sus componentes:
              br
              br
              |<em><b>1 Rect Transform:</b></em> es un componente en el que se establece la posicin y tamao de un  elemento en la interfaz del usuario, el ancho y el alto.
              br
              br
              |<em><b>2  Canvas:</b></em> es el rea bajo la cual se van a ubicar los elementos que va a contener la interfaz:
              br
              |*Opcin Screen Space - Overlay indica que la plantilla de Canvas se ajustara al tamao de la pantalla.
              br
              |*Opcin Screen Space - Camera indica que la plantilla de Canvas se ajustara para encajar en el plano de la cmara.
              br
              |*Opcin World Space - indica que la plantilla de Canvas tratara la imagen como un objeto plano en la escena.
              br
              br
              |<em><b>3 Canvas Scaler (Script):</b></em> tiene que ver con la densidad del pixel de los elementos que se coloquen dentro de la plantilla, esto hace que se vea con mayor o menor resolucin.
              br
              br
              |<em><b>4 Graphic Raycaster (<em>Script</em>):</b></em> controla los elementos grficos teniendo en cuenta los que se colocan en el fondo o delante. 
